<template>
  <div>
    <form  @submit.prevent="submit">
      <div class="row justify-content-center">
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow p-3 rounded-lg">
          <!-- INPUT -->
          <div class="sm-form">
            <input
              type="text"
              id="id"
              name="id"
              class="form-control form-control-sm"
              placeholder="id"
              v-model="vdata.id"
              disabled
            />
          </div>
          <div class="row">
            <div class="col-6">
              <div class="sm-form ">
                  <label for="judul">judul</label>
                  <input type="text" id="judul" name="judul" class="form-control form-control-sm" placeholder="judul" v-model="vdata.judul" >
              </div>
              <div class="sm-form ">
                  <label for="credential">credential</label>
                  <input type="text" id="credential" name="credential" class="form-control form-control-sm" placeholder="credential" v-model="vdata.credential" >
              </div>
              <div class="sm-form ">
                  <label for="link">Credential link/URL</label>
                  <input type="text" id="link" name="link" class="form-control form-control-sm" placeholder="link" v-model="vdata.link" >
              </div>
              <div class="sm-form ">
                  <label for="tanggal">tanggal</label>
                  <input type="date" id="tanggal" name="tanggal" class="form-control form-control-sm" placeholder="tanggal" v-model="vdata.tanggal" >
              </div>
            </div>
            <div class="col-6">
              <div class="sm-form ">
                  <label for="tanggal_expire">tanggal_expire</label>
                  <input type="text" id="tanggal_expire" name="tanggal_expire" class="form-control form-control-sm" placeholder="01/01/2021 - 02/04/2022 atau Selamanya" v-model="vdata.tanggal_expire" >
              </div>
              <div class="sm-form ">
                  <label for="teknologi">teknologi</label>
                  <input type="text" id="teknologi" name="teknologi" class="form-control form-control-sm" placeholder="teknologi" v-model="vdata.teknologi" >
              </div>
              <div class="sm-form ">
                <label for="gambar">gambar</label>
                <input type="file" id="filenya" name="gambar" class="form-control form-control-sm" placeholder="gambar" @change="upload" >
                <input type="text" id="gambar" name="gambar" class="form-control form-control-sm" placeholder="gambar" v-model="vdata.gambar" >
            </div>
              <div class="sm-form ">
                  <label for="urutan">urutan</label>
                  <input type="text" id="urutan" name="urutan" class="form-control form-control-sm" placeholder="urutan" v-model="vdata.urutan" >
              </div>
            </div>
          </div>
          <p class="p-3 pl-0">
            Keterangan
          </p>
          <wysiwyg v-model='vdata.keterangan' :options="{image: {  uploadURL: 'https://gis.officialapp.website/upload1.php',  dropzoneOptions: {}}}"/>
          <!-- END INPUT -->
          <!-- BUTTON PART -->
          <div class="mt-3 p-3">
            <button
              type="submit"
              class="btn btn-sm"
              :class="
                btn == 'tambah'
                  ? 'btn-primary'
                  : btn == 'update'
                  ? 'btn-success'
                  : 'btn-danger'
              "
            >
              <span class="typcn typcn-info"></span> Proses  {{ btn }}
            </button>
            <div class="float-right">
              <button
                type="button"
                v-show="btn!='tambah'"
                @click="
                  btn = 'tambah';
                  vdata = {};
                "
                class="btn btn-sm btn-dark"
              >
                <span class="typcn typcn-edit"></span> Tambah
              </button>
              <button
                type="button"
                @click="btn = 'delete'"
                v-show="btn!='tambah'"
                class="ml-3 btn btn-sm btn-danger"
              >
                <span class="typcn typcn-delete"></span> Delete
              </button>
            </div>
          </div>
          </div>
          <!-- END BUTTON PART -->
        </div>
        <div class="col-sm-9 p-2" style="font-size:12px;">
          <div class="shadow rounded-lg">
            <!-- TABLE PART -->
            <div v-if="datanya.length>0" class="p-2">
             <div class="text-right">
                <vue-json-excel :data="datanya">
                  <button type="button" class="btn btn-sm btn-dark">
                    Export <span class="typcn typcn-chart-area"></span>
                  </button>
                </vue-json-excel>
              </div>
                <Btables :tables="datanya"  :cari="['']" :hide="['keterangan']" @selected="ambil" ref="btable" />
            </div>
            <!-- END TABLE PART -->
         </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
// TOTURIAL 
// ganti source autophp, ganti source component btable, ganti path table sdb
import autophp from "@/plugins/autophp.js"; // ganti source nya
import Btables from "@/components/Btables.vue"; //component btable nya
let sdb = new autophp();
let urlnya ='https://gis.officialapp.website'
export default {
  components: {
    Btables,
  },
  data() {
    return {
      selected: false,
      vdata: {},
      btn: "tambah",
      datanya:[],
    };
  },
    layout:'app_admin',
  methods: {
     upload(){
        let file = document.querySelector("#filenya"); // berikan id pada input file
        sdb.collection('tbuser',false,urlnya).upload(file,true,true) // file,compress,scramble
        .then(res=>{
            console.log(res)
            this.vdata.gambar=res;
            this.$forceUpdate();
        })
    },
    getData(){
      sdb.collection("app_portofolio_sertifikat",false,urlnya).doc().select(`select * from app_portofolio_sertifikat where iduser='${this.$store.state.users.id}'`).then(res => {
        this.datanya=res;
        this.$forceUpdate();
      });
    },
    ambil(data) {
      this.vdata = data;
      this.btn = "update";
      this.$forceUpdate();
    },
    async submit() {
      let that=this
      that.vdata.iduser=this.$store.state.users.id
      if (confirm("Apakah yakin proses dilanjutkan ?")) {
        if (this.btn == "tambah") {
          delete this.vdata.id;
          await sdb
            .collection("app_portofolio_sertifikat",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "update") {
         await sdb
            .collection("app_portofolio_sertifikat",true,urlnya)
            .doc()
            .set(this.vdata)
              that.getData();
        } else if (this.btn == "delete") {
          await sdb
            .collection("app_portofolio_sertifikat",true,urlnya)
            .doc(this.vdata.id)
            .delete()
              that.getData();
        }
      }
    },
  },
  async mounted() {
          this.getData();
  },
};
</script>